	var schedule = function() {
		var headerHeight = $(".schedule .results .outbound header").outerHeight() + $(".schedule_container > header").outerHeight();
		var windowHeight = 0;
		var scrollAnimation = new Object();
		scrollAnimation.kill = function() {

		}
		function control(e) {
			
		}
		function resizeHandler(e) {
			windowHeight = $(window).innerHeight();
			// headerHeight = $(".schedule .results .selecting header").outerHeight() + $(".schedule_container > header").outerHeight();
			// var matrix = $(".schedule_container .results").css("transform").split(", ");
			var extraHeaderHeight = 0;
			var headerheight = 0;
			var headerMinHeight = 80;
			// headerHeight=parseInt(matrix[5]) + parseInt($(".schedule_container .results").css("top"));

			setTimeout(function() {
				// $("body").scrollTop(0);
				$(".schedule").each(function() {
					scrollAnimation = TweenMax.to($("body"), 1, {scrollTo:{y:0, x:0}, ease:Power2.easeInOut});
				});
			},400);
			$("body").removeClass("final");
			$(".schedule header.itinerary_container").removeClass("final");
			if ($("body").hasClass("step1")) {
				extraHeaderHeight = $("header.outbound").outerHeight();
				headerHeight =  5+headerMinHeight;
			} else if ($("body").hasClass("step2")) {
				extraHeaderHeight = $("header.returning").outerHeight();
				headerHeight = 95+headerMinHeight;
			} else if ($("body").hasClass("step3")) {
				extraHeaderHeight = $("header.ticket_options ").outerHeight();
				headerHeight = 182+headerMinHeight;
			} else if ($("body").hasClass("step4")) {
				extraHeaderHeight = 0;
				headerHeight =  520+headerMinHeight;
				
				setTimeout(function() {
					$(".schedule").addClass("final");
				},400);
			}

			$(".schedule header.itinerary_container").css("max-height",headerHeight);
			$(".schedule div.itinerary_background").css("transform","translateY(-"+($(".schedule div.itinerary_background").outerHeight() - headerHeight)+"px)");
			$("header.table").css("transform","translate3d(0,"+headerHeight+"px,0.1px)");
			$(".schedule .results").css("transform","translate3d(0,"+(headerHeight+extraHeaderHeight)+"px,-0.1px)");
			// $(".outbound ul, .returning ul, .ticket_options ul").css("height",windowHeight-(headerHeight+extraHeaderHeight));
			// $(".results").css("height",windowHeight-headerHeight);
		}
		function scheduleStepHandler(e) {
			var target = e.target;
			if (target.tagName != "LI") {
				target = target.parentNode;
				if (target.tagName != "LI") {
					target = target.parentNode;
				}
			}
			var container = target.parentNode.parentNode;	
			var activeClass = container.className;
			var currentStep = $(container).index()+1;
			var nextStep = currentStep + 1;
			$("body").removeClass("step"+currentStep).addClass("step"+nextStep);
			$(target).siblings().addClass("faded");

			$("datetime",$(target)).first().each(function() {
				$(".itinerary div."+activeClass+" datetime").first().html($("datetime",$(target)).first().html());
				$(".itinerary div."+activeClass+" datetime").last().html($("datetime",$(target)).last().html());
			});
			setTimeout(function() {
				$("."+activeClass,$(".results")).css("display","none").next().css("display","block");
			},400);
			if (nextStep == 4) {
				if ($(target).hasClass("upgrade")) {
					var ticket = $(".name", $(target).prev() ).html()+ " First";
				} else {
					var ticket = $(".name",$(target)).html();
				}
				var price = $(".price",$(target)).html();
				$(".ticket").html(ticket);
				$(".price").html(price);
				resizeHandler();
				stepAnimateFast(".itinerary");
			}
			resizeHandler();
		}

		return {
			Init: function() {
				$(window).bind("mousewheel",function(e) {
					scrollAnimation.kill();
				});
				$("li .changes").bind("click",function(e) {
					enterJourneyScreen();
					e.preventDefault();
					e.stopPropagation();
					return false;
				});
			
				resizeHandler();
				$(".results li").bind("click",scheduleStepHandler);

				// going back controls
				$(".schedule .itinerary_container .stations").bind("click",function(e) {
					// $("body").removeClass("step2").removeClass("step3").removeClass("step4");
					// $(".outbound li, .returning li, .ticket_options li").removeClass("faded");
					// $(".opacityZero").css("opacity",0);
					// closeSchedule();
					// setTimeout(function() {
					// 	enterPlanning();
					// },1200);
					// e.preventDefault();
					// $(".outbound",$(".results")).css("display","block");
					// document.location.href="/planning.php";
 					window.history.back();
 					e.preventDefault();
 					return false;
				});
				$(".itinerary .outbound_train, .itinerary datetime.outbound, .itinerary div.outbound").bind("click",function() {
					setTimeout(function() {
						$(".outbound",$(".results")).css("display","block").css("opacity",0);
					},500);
					setTimeout(function() {
						$(".outbound",$(".results")).css("opacity",1);
					},650);
					$("body").removeClass("step1 step2 step3 step4");
					$("body").addClass("step1");
					$(".outbound li, .returning li, .ticket_options li").removeClass("faded");
					$(".schedule_container .opacityZero").css("opacity",0);
					
					resizeHandler();
				});

				$(".itinerary .returning_train, .itinerary datetime.returning, .itinerary div.returning").bind("click",function() {
					
					setTimeout(function() {
						$(".returning",$(".results")).css("display","block").css("opacity",0);
					},500);
					setTimeout(function() {
						$(".returning",$(".results")).css("opacity",1);
					},650);
					$("body").removeClass("step1 step2 step3 step4");
					$("body").addClass("step2");
					$(".schedule_container .opacityZero").css("opacity",0);
					$(".outbound li, .returning li, .ticket_options li").removeClass("faded");
					setTimeout(function() {
						// $(".returning").removeClass("final").addClass("selecting");
					},300);
					resizeHandler();
				});

				$("h3.ticket").bind("click",function() {
					setTimeout(function() {
						$(".ticket_options",$(".results")).css("display","block").css("opacity",0);
					},500);
					setTimeout(function() {
						$(".ticket_options",$(".results")).css("opacity",1);
					},650);
					$("body").removeClass("step1 step2 step3 step4");;
					$("body").addClass("step3");
					$(".schedule_container .opacityZero").css("opacity",0);
					$(".ticket_options li").removeClass("faded");

					setTimeout(function() {
						// $(".ticket_options").removeClass("final").addClass("selecting");
					},300);
					resizeHandler();
					
				});
				$(".last-choices button").bind("click",function() {
					$(".last-choices").css("opacity",0);
					setTimeout(function() {
						$(".last-choices").css("display","none");
						$(".ticket_information").css("display","block").css("opacity",0);

						$(".outbound_train, .itinerary datetime.outbound, .itinerary div.outbound, .returning_train, h3.ticket, .stations, .itinerary .returning_train, .itinerary datetime.returning, .itinerary div.returning",$(".schedule")).addClass("final").unbind("click");
						setTimeout(function() {
							$(".ticket_information").css("opacity",1);
							$(".cards").addClass("active");
						},20);

					},200);
				});
			},
			sizeUp: function() {
				resizeHandler();
			}
		}
	}();
$(function() {
	schedule.Init();
	$("div, header, ul").bind("scroll",function(e) {
		console.log("scrolling",e.target);
	})
});

	
	
