function closeDestinationScreen() {
	$(".remove").animate({ opacity: 0},200);
	$(".search_1 .people").css("transition-duration","0.6s").css("transition-timing-function","cubic-bezier(.73,0,1,.46)").css("transform","translateY(800px)");
	setTimeout(function() {
		$(".search_1 .background").css("transition-duration","0.6s").css("transition-timing-function","cubic-bezier(.73,0,1,.46)").css("transform","translateY(800px)");
	},100);
	setTimeout(function() {
		$(".search_1").animate({ opacity: 0},300);
	},300);
	setTimeout(function() {
		$(".search_station").animate({ opacity: 0},300);
	},600);
	setTimeout(function() {
		$(".search_station").css("display","none");
	},900);
}
function closeHomeScreen() {
	$(".get_tickets, div.changes, .scroll-indicator").css("opacity",0);
	$("body.home .top").css("transition-duration","0.4s").css("transition-timing-function","cubic-bezier(.73,0,1,.46)").css("transform","translateY(-500px)").css("opacity",0);
	$("body.home .bottom").css("transition-duration","0.4s").css("transition-timing-function","cubic-bezier(.73,0,1,.46)").css("transform","translateY(500px)").css("opacity",0);
	
}
first=true;
function enterHomeScreen(delay) {
	console.log("enter home");
	lastActiveScreen="home";
	$("body.home .opacityZero").each(function(i, e) {
		
			$(e).css("opacity", 1);
		
	});
	if (typeof delay == "undefined") delay=1200;
	setTimeout(function() {
		// $("body.home").css("opacity",0).css("display","block").animate({ opacity: 1},300);
		setTimeout(function() {
			$(".home div.changes, .scroll-indicator").css("opacity",1);
		},350);
		if (first) {
			clock_slider.Init($(".clock_slider").eq(0),0);
			first = false;
		}
	},(delay));
}

function enterJourneyScreen() {
	$(".burger img").attr("src","img/icon-menu-close.svg");
	$(".home div.changes").css("opacity",0);
	$(".journey .opacityZero").each(function(i, e) {
		setTimeout(function(obj) {
			$(obj).animate({ opacity: 1},300);

		},(i*270)+500,e);
	});
	$(".journey").addClass("active");
	$(".journey").scrollTop(($(".journey")[0].scrollHeight - $(window).height())/2);
	$(".journey ul li:nth-child(4)").addClass("travel");
	$(".journey ul .current_status").addClass("travel");
	$(".burger").unbind();
	$(".burger").bind("click",function() {
		if (lastActiveScreen=="home") {
			var time = new Date().getTime();
			closeJourneyScreen();
			setTimeout(enterHomeScreen,550,0);
			setTimeout(function() {
				
				setTimeout(function() {
					$(".home .top").css("transition-duration","0.4s").css("transition-timing-function","cubic-bezier(0,.91,.12,.99)");
					$(".home .bottom").css("transition-duration","0.4s").css("transition-timing-function","cubic-bezier(0,.91,.12,.99)");
				},100);
				setTimeout(function() {
					$(".home .top").css("transform","translateY(0)").css("opacity",1);
					$(".home .bottom").css("transform","translateY(0)").css("opacity",1);
				},110);
			},200);
		} else if (lastActiveScreen=="planning") {
			closeJourneyScreen();
		}
		$(".burger").unbind();
		$(".burger").bind("click",function() {
			closeWhatEver();
			setTimeout(initiateScreen,300);
		});
	});
}
